@import url("https://fonts.googleapis.com/css2?family=Muli:ital,wght@0,400;0,500;0,700;0,800;1,300&display=swap");

html {
  overflow: -moz-scrollbars-vertical;
  overflow-y: scroll;
}

body {
  font-family: "Muli", Arial, Helvetica, sans-serif;
  color: #0c3968;
  background-color: #204b99;
}

.nav-brand {
  height: 30px;
  width: auto;
}

.toast {
  z-index: 999 !important;
  right: 0;
  top: 110px;
}

.container-fluid {
  padding-top: 150px;
  padding-bottom: 100px;
}

.card {
  border-radius: 10px;
}

.list-group-item {
  border-radius: 10px !important;
}

.text-dark {
  color: #0c3968 !important;
}

.bg-blue {
  background-color: #204b99;
}

.text-blue {
  color: #204b99;
}

.text-light {
  color: #8ca1ca !important;
}

.title-text {
  font-size: 24px;
}

.sm-title-text {
  font-size: 16px;
}

.xs-title-text {
  font-size: 13px;
}

.text-minus {
  color: #f5787e;
}

.text-plus {
  color: #33ad79;
}

.btn-outline-blue {
  border: 2px solid #204b99;
  border-radius: 5px;
  color: #204b99;
  font-weight: 700;
}

.btn-outline-blue:hover {
  background-color: #204b99;
  color: #fff;
}

.lg-text {
  font-size: 50px;
}

.sm-input {
  max-width: 80px;
}

/* Medium devices (tablets, less than 992px) */
@media (max-width: 991.98px) {
  .lg-text {
    font-size: 38px;
  }
  .title-text {
    font-size: 26px;
  }
  .sm-title-text {
    font-size: 14px;
  }
}

/* Small devices (landscape phones, less than 768px) */
@media (max-width: 767.98px) {
  .lg-text {
    font-size: 30px;
  }
  .title-text {
    font-size: 18px;
  }
}
